import React from 'react';
import {Route,Redirect} from 'react-router-dom'

const ProtectedCandidat = ({isAuth: isAuth, component: Component, ...rest}) => {
    return (
          <Route
            {...rest}
            render={(props) =>{
                if (isAuth) {
                    return <Component />
                } else {
                    return (
                        <Redirect to={{pathname: "/candidats/connexion", state:{ from: props.location} }} />
                    )
                }
            }}
          
          />
    )
}

export default ProtectedCandidat
